/* =====================
    common
===================== */
//btn
.btn {
    width: 50%;
    padding: 12px 0;
    font-size: 16px; //14px //28px
    text-align: center;
    &-wrap {
        &--one {
            &> * {
                width: 100%;
                border-bottom-left-radius: 8px;
            }
        }
    }
    &.btn__cancel {
        border-bottom-left-radius: 8px;
        background-color: #dbdbdb;
    }
    &.btn__submit {
        border-bottom-right-radius: 8px;
        background-color: #404040; color: #fff;
    }
}
.btn__zoom {
    width: 40px;
    height: 40px;
    background-size: 100% 100%;
    &--in {background-image: url(#{$image}/map/i_zoom--in.png);}
    &--out {background-image: url(#{$image}/map/i_zoom--out.png);}
}
.btn-close {
    display: block;
    width: 15px;
    height: 15px;
    background-repeat: repeat;
    background-position: center;
    background-size: cover;
    &--white {background-image: url(#{$image}/dashboard/i_close--w.png);}
    &--black {background-image: url(#{$image}/dashboard/i_close--b.png);}
}

//form
.form-input {
    width: 100%;
    padding: 10px;
    border: 1px solid #c4c4c4;
    font-size: 13px; //26px
}
.form-select {
    width: 100%;
    padding: 10px;
    border: 1px solid #c4c4c4;
    font-size: 13px; //26px
}
.form-radio {
    display: none;
    visibility: hidden;
    &+ label {
        display: flex;
        align-items: center;
        span {
            display: block;
            width: 18px; //28px
            height: 18px; //28px
            margin-right: 5px;
            border-radius: 50%;
            border: 5px solid #ebebeb;
            background-color: #fff;
        }
    }
    &:checked + label {
        span {border-color: #2FBFDE;}
    }
}

//select
.select-ui {
    button {
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 100%;
        padding: 10px 15px;
        color: #000;
        font-size: 14px; //28px
    }
    img {
        width: 12px;
        vertical-align: middle;
    }
}
//icon
.notice {
    height: 24px; line-height: 24px;
    padding-left: 25px !important;
    background-size: 20px 20px;
    background-position: left center;
    background-repeat: no-repeat;
    &--red {background-image: url(#{$image}/i_notice--red.png);}
    &--gray {background-image: url(#{$image}/i_notice--gray.png);}
}
.icon-info {
    height: 30px; line-height: 30px;
    padding-left: 30px;
    background-image: url(#{$image}/map/img_info.png);
    background-repeat: no-repeat;
    background-size: 28px 26px;
}
.icon-point {
    display: inline-block;
    height: 17px; line-height: 17px;
    margin: 8px 0;
    margin-right: 15px;
    padding-left: 18px;
    background: no-repeat left / 14px 17px url(#{$image}/dashboard/i_point.png);;
    font-size: 14px;
    color: #000;
}
.state {
    padding-left: 5px;
    padding-right: 15px;
    background-position: right center;
    background-repeat: no-repeat;
    background-size: 13px 15px;
    &-up {
        color: #d67c89;
        background-image: url(#{$image}/dashboard/i_state-up.png);
    }
    &-down {
        color: #41a8b8;
        background-image: url(#{$image}/dashboard/i_state-down.png);
    }
    &::before {content: none !important;}
}