
@import './common';
/* =====================
    header
===================== */
#header {
    .header {
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 15px 15px 10px; //30px 20px
        border-bottom: 1px solid #365375;
        background-color: #133761;
        a {display: inline-block;}
    }
}
.logo {
    p {
        color: #fff;
        font-size: 18px;
        font-weight: 500;
    }
    img {width: 70px;}
}
.btn {
    &-search {
        margin-right: 10px; //20px
        img {width: 25px;}
    }
    &-menu {
        img {width: 22px;}
    }
    &-close {
        img {width: 18px;}
    }
}
.nav {
    overflow-y: scroll;
    z-index: 999;
    position: fixed;
    top: 0;
    left: -100%;
    width: 100%;
    height: 100%;
    background-color: #f5f5f5;
    &> div:first-child {
        & > div {
            padding: 20px 30px 10px 15px; //40px 30px
        }
    }
}
.gnb {
    width: 100%;
    padding: 0 30px; //60px
    padding-bottom: 65px;
    border-bottom: 1px solid #98abba;
    a {
        color: #fff;
        font-weight: 500;
    }
    &> li {
        &> a {
            display: flex;
            justify-content: space-between;
            padding: 14px 5px 14px 0;
            border-bottom: 1px solid #275a82;
            font-size: 16px; //32px;
            img {width: 8px;}
            span.on {
                position: relative;
                padding-right: 10px; //20px
                &::after {
                    content: '';
                    display: block;
                    position: absolute;
                    top: 10px;
                    right: 0;
                    width: 5px; //10px
                    height: 5px; //10px
                    border-radius: 50%;
                    background-color: #ffe402;
                }
            }
        }
    }
}
.sub-gnb {
    display: flex;
    flex-flow: row wrap;
    border-left: 1px solid #376589;
    li {
        width: 50%;
        border-bottom: 1px solid #376589;
        border-right: 1px solid #376589;
        background-color: #0f4876;
        a {
            display: block;
            padding: 15px 20px; //30px 40px
            font-size: 15px;
        }
    }
}
.search {
    width: calc(100% - 34px);
    margin: 0 auto;
    margin-top: -40px;
    margin-bottom: 37px;
    padding: 18px; //37px
    border-radius: 15px;
    background-color: #fff;
    box-shadow: 0px 5px 10px 5px rgba(179, 179, 179, 0.48);
    &> * {
        display: flex;
    }
    &__list {
        justify-content: space-between;
        margin-bottom: 14px;
        padding: 0 10%;
        a {
            display: block;
            padding: 7px 0;
            color: #363636;
            font-size: 15px;
        }
    }
    &__form {
        position: relative;
    }
    .form-input {
        height: 40px;
        border-radius: 10px;
        border: 1px solid #e4e2ee;
        background-color: #f5f5f5;
    }
    .search__btn {
        position: absolute;
        top: 10px;
        right: 10px;
        img {width: 20px;}
    }
}
.info {
    margin-bottom: 100px;
    text-align: center;
    a {
        display: block;
        margin-bottom: 5px;
        color: #363636;
        font-size: 14px;
    }
    p {
        color: #999999;
        font-size: 10px;
    }
}
.navi {
    background-color: #d6e4fa;
    &> div {
        &> * {border-right: 1px solid #b6c8e4;}
    }
    a {display: block;}
    select {
        border: none;
        background: transparent;
        &:focus, &:hover, :active {
            border: none;
            outline: none;
        }
    }
    .home {
        padding: 12px 13px;
        border-left: none;
        img {width: 15px;}
    }
    .path {
        position: relative;
        width: 41%;
        &> a {
            //width: calc(100% - 42px);
            display: flex;
            align-items: center;
            justify-content: space-between;
            padding: 11px 10px;
            color: #363636;
            font-size: 14px;
            img {width: 14px; vertical-align: middle;}
            span {
                &:first-child {margin-right: 10px;}
                letter-spacing: -1px;
            }
            &:hover {
                img {
                    -webkit-transform: rotate(180deg);
                    transform: rotate(180deg);
                }
            }
        }
        &__con {
            z-index: 99;
            position: absolute;
            width: 100%;
            padding: 5px 0;
            background-color: #e5effe;
            &> a {
                display: block;
                padding: 6px 10px;
                color: #515355;
                font-size: 14px;
            }
        }
    }
    .sub-path {
        width: calc(100% - (41% +  41px));
        //border-right: 1px solid #b6c8e4;
    }
}

.map__above {
    &.fixed {
        z-index: 1000;
        position: fixed; left: 0; top: 0;
        width: 100%;
    }
}

/* =====================
    footer
===================== */
.footer {
    &__link { 
        display: flex;
        li {
            width: 33.333%;
            &:not(:first-child) {
                a {
                    border-left: 1px solid #cdcdcd;
                }
            }
        }
        a {
            display: block;
            padding: 9px 0;
            border-bottom: 1px solid #cdcdcd;
            background-color: #f0f1f2;
            color: #000;
            font-size: 15px; //30px
            text-align: center;
        }
    }
    padding: 20px 0 30px;
    //border-bottom: 1px solid #adadad;
    background-color: #333333;
    text-align: center;
    .logo {margin-bottom: 10px;}
    p {
        margin-bottom: 13px;
        color: #adadad;
        font-size: 13px;
    }
    span {
        display: block;
        color: #727272;
        font-size: 13px;
    }
    &__btn {
        display: flex;
        justify-content: center;
        margin-bottom: 15px;
        &> * {
            padding: 7px 15px;
            border-radius: 3px;
        }
    }
    &__select {
        position: relative;
        border: 1px solid #191919;
        background-color: #272727;
        &> * {
            color: #fff;
            font-size: 13px;
        }
        img {
            width: 15px;
            margin-left: 3px;
            vertical-align: middle;
        }
        &__con {
            position: absolute; top: -167px; left: -1px;
            width: calc(100% + 2px);
            border-top-left-radius: 3px;
            border-top-right-radius: 3px;
            border: 1px solid #131313;
            background-color: #272727;
            a {
                display: block;
                padding: 8px 15px;
                background: no-repeat 97% center / 26px url(#{$image}/main/i_link.png);
                color: #969696;
                text-align: left;
            }
        }
    }
    .btn-pc {
        margin-right: 5px;
        border: 1px solid #555555;
        color: #fff;
        font-size: 13px;
    }
}

/* =====================
    modal
===================== */
.modal {
    z-index: 998;
    position: fixed;
    top: 20%;
    left: 50%;
    transform: translateX(-50%);
    width: 80%;
    border-radius: 8px;
    background-color: #fff;
    box-shadow: 0 4px 6px 0 rgba(88, 88, 88, 0.48);
    &__header {
        padding: 10px 10px 5px; //55px
        border-bottom: 1px solid #c4c4c4;
    }
    &__tit {
        color: #585f6e;
        font-size: 13px; //26px
    }
    &__body {
        padding: 5px;
        img {
            width: 60px;
        }
    }
}

//dim
.dim {
    z-index: 99;
    display: none;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.6);
}

@media screen and (max-width:360px) {
    .footer {
        &__link {
            a {font-size: 12px;}
        }
    }
    .navi {
        .path {
            &> a {
                padding: 10px 2px;
            }
        }
    }
}

// rule
.rule {
    padding: 15px 10px;
    &__list {
        margin-bottom: 20px;
        li {
            position: relative;
            padding: 8px 20px;
            border-bottom: 1px solid #cdcdcd;
            &::before {
                content: '';
                position: absolute; top: 15px; left: 10px;
                width: 3px; height: 3px;
                border-radius: 50%;
                background-color: #000;
            }
        }
        a {
            color: #000;
            font-size: 14px;
            letter-spacing: -1.5px;
        }
    }
    &__con {
        div {&:not(:first-child) {margin-top: 15px;}}
        h2 {
            margin: 15px 0;
            font-size: 21px;
            text-align: center;
        }
        h3 {
            margin-bottom: 5px;
            font-size: 18px;
        }
        p {
            color: #585f6e;
            font-size: 13px;
            letter-spacing: -1px;
        }
        ul {padding-left: 10px;}
    }
}

//privacy
.privacy {
    padding: 15px 10px;
    &__info {
        padding: 20px;
        background-color: #ecf2fd;
        font-size: 14px;
    }
    &__list {
        margin-bottom: 20px;
        li {
            padding: 8px 10px;
            border-bottom: 1px solid #cdcdcd;
        }
        a {
            color: #000;
            font-size: 14px;
            letter-spacing: -1.5px;
        }
    }
    &__con {
        > div {
            &:not(:first-child) {margin-top: 15px;}
            h3 {
                margin-bottom: 5px;
                font-size: 18px;
            }
            p {
                color: #585f6e;
                font-size: 13px;
                letter-spacing: -1px;
            }
            strong {
                display: block;
                font-size: 14px;
            }
            ul {padding-left: 10px;}
        }
    }
}
.btn-scroll {
    z-index: 999;
    position: fixed;
    bottom: 10%;
    right: 3%;
    a {
        display: block;
        &:first-child {
            margin-bottom: 15px;
        }
    }
    img {width: 40px;}
}